.Header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding: 16px 0;
    margin-bottom: 20px;
    border-bottom: 4px solid #3f51b5
}

.Header__logo {
    color: #424242;
    text-transform: capitalize
}

.Navigation {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row-reverse;
    flex-direction: row-reverse;
    list-style: none
}

.Navigation__item {
    margin-left: 16px
}

.Navigation__link {
    position: relative;
    display: block;
    text-transform: uppercase;
    text-decoration: none;
    color: #424242;
    font-size: 20px
}

.Navigation__link:after {
    position: absolute;
    top: 47px;
    left: 0;
    content: "";
    width: 0;
    height: 4px;
    background: #ff5722;
    -webkit-transition: width .3s linear;
    -o-transition: width .3s linear;
    transition: width .3s linear
}

.Navigation__link:hover {
    color: #ff5722
}

.Navigation__link:hover:after {
    width: 100%
}

.MovieInfo {
    -ms-flex: 0 0 55%;
    flex: 0 0 55%
}

.MovieInfo__title {
    width: 90%;
    line-height: 1.5;
    margin-bottom: 10px;
    white-space: wrap
}

.MovieInfo__descr {
    margin-bottom: 20px;
    line-height: 1.3
}

.MovieInfo__rating {
    margin-bottom: 16px;
    font-size: 18px
}

.ChipsList {
    display: -ms-flexbox;
    display: flex;
    width: 85%;
    padding: 8px 0;
    list-style: none
}

.ChipsList__item {
    margin-right: 4px;
    padding: 4px 8px;
    font-size: 14px;
    border-radius: 2px;
    background-color: #3f51b5;
    color: #fff
}

.MovieCard {
    position: relative;
    -ms-flex: 0 0 32%;
    flex: 0 0 32%;
    margin-right: 2%;
    padding: 8px;
    margin-bottom: 16px;
    color: #424242;
    -webkit-box-shadow: 0 0 2px 2px rgba(33, 33, 33, .5);
    box-shadow: 0 0 2px 2px rgba(33, 33, 33, .5);
    background-color: #fff;
    border-radius: 2px
}

.MovieCard:nth-child(3n) {
    margin-right: 0
}

.MovieCard__btn {
    position: absolute;
    outline: 0;
    border: 0;
    background-color: transparent;
    cursor: pointer
}

.MovieCard__btn--del {
    background-color: #ff5722;
    color: #fff;
    top: 8px;
    right: 8px;
    padding: 4px 10px;
    font-size: 16px;
    border-radius: 4px;
    text-transform: capitalize;
    -webkit-box-shadow: 0 2px 2px 0 rgba(33, 33, 33, .5);
    box-shadow: 0 2px 2px 0 rgba(33, 33, 33, .5);
    -webkit-transition: all .3s linear;
    -o-transition: all .3s linear;
    transition: all .3s linear
}

.MovieCard__btn--del:hover {
    -webkit-box-shadow: 0 4px 2px 0 rgba(33, 33, 33, .5);
    box-shadow: 0 4px 2px 0 rgba(33, 33, 33, .5);
    background-color: #3f51b5
}

.MovieCard__img {
    display: block;
    width: 40%;
    height: auto
}

.MovieList {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -ms-flex: 0 0 62%;
    flex: 0 0 62%
}

.MovieForm {
    padding: 16px;
    -ms-flex: 0 0 35%;
    flex: 0 0 35%;
    border-radius: 2px;
    -webkit-box-shadow: 0 0 2px 2px rgba(33, 33, 33, .5);
    box-shadow: 0 0 2px 2px rgba(33, 33, 33, .5);
    text-align: center
}

.MovieForm__label {
    display: block;
    margin-bottom: 20px;
    color: #424242;
    text-align: left
}

.MovieForm__input, .MovieForm__txtarea {
    display: block;
    width: 100%;
    margin-top: 4px;
    padding: 4px;
    font-size: 20px;
    color: #424242;
    font-family: Roboto, sans-serif
}

.MovieForm__txtarea {
    resize: none
}

.MovieForm__input:focus, .MovieForm__txtarea:focus {
    outline-color: #303f9f
}

.MovieForm__submit {
    outline: 0;
    border: 0;
    padding: 10px 20px;
    border-radius: 2px;
    text-transform: uppercase;
    color: #fff;
    font-size: 16px;
    background-color: #3f51b5;
    cursor: pointer;
    -webkit-box-shadow: 0 3px 3px 0 rgba(0, 0, 0, .3);
    box-shadow: 0 3px 3px 0 rgba(0, 0, 0, .3);
    -webkit-transition: all .2s linear;
    -o-transition: all .2s linear;
    transition: all .2s linear
}

.MovieForm__submit:hover {
    background-color: #ff5722;
    -webkit-box-shadow: 0 5px 5px 0 rgba(0, 0, 0, .3);
    box-shadow: 0 5px 5px 0 rgba(0, 0, 0, .3)
}

.App {
    max-width: 1280px;
    margin: auto;
    padding: 0 10px
}

.App__body {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-align: start;
    align-items: flex-start
}

* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 0;
    margin: 0
}

body {
    font-family: Roboto, sans-serif;
    background-color: #fff
}



